import React from 'react';
import { StaticQuery, graphql } from 'gatsby';
import PropTypes from 'prop-types';
import styled from 'styled-components';
import { Container } from '@components/global';
import Fade from 'react-reveal/Fade';

const Paragraph = ({ header, paragraph, children, caption}) => (
  <StaticQuery
  query={graphql`
    query {
      allFile(filter: { sourceInstanceName: { eq: "photos" } }) {
        edges {
          node {
            relativePath
            childImageSharp {
              fluid(maxWidth: 400, maxHeight: 400) {
                ...GatsbyImageSharpFluid
              }
            }
          }
        }
      }
    }
  `}
  render={data => (
    <Container>
      <h3>{header}</h3>
      <HeaderWrapper>
        <Fade>
          <Grid>
            {paragraph.map(({ content }) => {
              return (
                <div>
                  <Text>{content()}</Text>
                </div>
              );
            })} 
            <Art>
              {children}
              <Caption>{caption}</Caption>
            </Art>
            </Grid>
        </Fade>
      </HeaderWrapper>
    </Container>
    )}
  />
);

Paragraph.propTypes = {
  children: PropTypes.element.isRequired,
};

const HeaderWrapper = styled.div`
  padding: 35px 0 20px 0;
  font-weight: 400;
  vertical-align: middle;
  color: ${props => props.theme.color.grayscale.S1000};
  @media (max-width: ${props => props.theme.screen.md}) {
    max-width: 100%;
    padding: 15px 0;
    margin-left: 0%;
  }
`;

const Grid = styled.div`
  display: grid;
  grid-template-columns: 5fr 4fr;
  grid-gap: 50px;
  text-align: left;
  align-items: center;
  justify-items: left;

  @media (max-width: ${props => props.theme.screen.md}) {
    grid-template-columns: 1fr;
    text-align: left;
    margin-bottom: 96px;
    grid-gap: 25px;

    &:last-child {
      margin-bottom: 24px;
    }
  }
`;

const Art = styled.figure`
  margin: 30px 0;
  max-width: 1380px;
  width: 100%;
  img{
    width: 100%;
  }
`;

const Text = styled.figure`
  margin: 18px 0;
  @media (max-width: ${props => props.theme.screen.md}) {
    margin: 14px 0;
  }
`;

const Caption = styled.div`
  padding-top:5px;
  padding-right: 30%;
  ${props => props.theme.font_size.xsmall};
`;

export default Paragraph;
