import React from 'react';
import { StaticQuery, graphql } from 'gatsby';
import Img from 'gatsby-image';
import SEO from '@common/SEO';

import Layout from '@common/Layout';
import Navbar from '@common/Navbar';
import Link2Act from '@common/Link2Act';
import Footer from '@common/Footer';

import { Section} from '@components/global';
import Paragraph from '@ui/paragraph';
import SectionHeader from '@ui/sectionheader';

import about from '@static/og/about.jpg';


const paragraphTxt = [
  {
    content: () => ( 
      <>
      <p>We are Yen-Tsen Liu, design and sociology major, and Kevin Cheng, physics and electronics major.</p>
      <p>We are long time friends sharing a love for music and electronics.</p>
      <p>Rearia was born in 2017, when we finally decided to pursue our long time dream.</p>
      <p>We are long time friends sharing a love for music and electronics.</p>
      <p>Our dream was to make a device that would be universal to most hearing conditions, deliver excellent sound quality and would not discriminate hearing aid users or medicalize their devices unnecessarily.</p>
      <p>A common problem for hearing aid users is they are unable to wear earphones, which is the reason they need to use a device called "streamer". This device works as intermediary between hearing aid and the audio source, such as TV or phone, transmitting the audio from source to the hearing aid.</p>
      <p>However, the reason we wanted to re-invent the streamer is that they are not universal, they only work for specific hearing conditions. There are some different models, but their sound quality is inferior. Most streamers don't work in combination with normal earphones, and so listening music with single hearing aid and normal earphones is impossible.</p>
      <p>Another aspect is that these good quality ones are mostly made by medical device companies, and it can be seen in they style and design.</p>
      <p>We wanted to create a modular streamer that would allow people to wear the style they want and provide excellent sound quality in a completely modular product. We also wanted to free it from the medical image, and make it like any other audio device that the user can customize and express their style.</p>
      <p>Now we have been working on this project for 3 years and have come a long way. Finally our product is ready, and we call it Hai. It has achieved everything we wanted to accomplish in our original vision."</p>
      </>
    ), 
  },
];



const AboutUs = () => (
  <StaticQuery
    query={graphql`
      query {
        About: file(
          sourceInstanceName: { eq: "photos" }
          name: { eq: "about" }
        ) {
          childImageSharp {
            fluid(maxWidth: 1450) {
              ...GatsbyImageSharpFluid
            }
          }
        },      
      }
    `}
    render={data => (
      <Layout>    
        <SEO title="About | Rearia - Hearing Technology for Everyone" desc="Our dream was to make a device that would be universal to most hearing conditions, deliver excellent sound quality and would not discriminate hearing aid users or medicalize their devices unnecessarily." img={about} url="https://rearia.io/about/"/>
        <Navbar />
        <Section id="about_us">
          <SectionHeader 
            header="We are Rearia" 
          />
          <Paragraph paragraph={paragraphTxt}>
            <Img fluid={data.About.childImageSharp.fluid}/>
          </Paragraph>
        </Section>
        <Link2Act/>
        <Footer />
      </Layout>
    )}
  />
);

export default AboutUs;
